import React from "react";
import { useState, useEffect } from "react";
import "../index.css"
import Button from "./Button";



function PIN() {

  const [pin, setPin] = useState('');
  const [attempt, setAttempt] = useState(0);
  const [lock, setLock] = useState(false);
  const [message, setMessage] = useState('  ');
  const correctPin = "1234";

  // Reset the state if the "number" is "C"
  // otherwise add the number to the state string
  function handleClick(e) {
    const { number } = e.target.dataset;
    if (number === 'C') {
      setPin([]);
    } else if (number === "E") {
      setMessage("Enter a number!")
    } else {
      setPin(`${pin}${number}`);
    }
  }

  useEffect(() => {
    if (attempt < 3) {
      if (pin.length === 4) {
        if (pin !== correctPin) {
          setAttempt(attempt + 1);
          setPin('');
          setMessage('ERROR');
          setTimeout(() => setMessage(''), 3000);

        } else {
          setMessage('OK');
          setPin('');
          setLock(true);
          setTimeout(() => setLock(false), 5000);
          setTimeout(() => setMessage(''), 5000);
          setAttempt(0);
        }
      }
    } else {
      setMessage("LOCKED");
      setLock(true);
      setTimeout(() => setLock(false), 30000);
      setTimeout(() => setMessage(''), 30000);
      setAttempt(0);

    }
  }, [pin]);


  function maskedPin() {
    let str = '';
    for (let i = 0; i < pin.length; i++) {
      str += '*';
    }
    return str;
  }


  // Create a new array and map over the elements
  // to create some buttons
  return (
    <div>
      <div className="texting">
        <h2>PINPAD APP</h2>
        <h3>You have 3 chances, after 3 wrong pins, buttons get locked for 30 seconds!</h3>
      </div>
      <div className="message">
        <div>{maskedPin()}</div>
        <div>{message}</div>
      </div>
      <div className="wrapping">
        <div className="grid">
          {new Array(9).fill('').map((el, i) => (
            <Button lock={lock} number={i + 1} handleClick={handleClick} />
          ))}
          <Button lock={lock} number="C" handleClick={handleClick} />
          <Button lock={lock} number="0" handleClick={handleClick} />
          <Button lock={lock} number="E" handleClick={handleClick} />
        </div>
      </div>
    </div>
  );
}



export default PIN;
